import { createStore } from 'vuex';

export default createStore({
  state: {
    user: null, // 初始状态下用户为空
  },
  mutations: {
    setUser(state, user) {
      state.user = user; // 将传入的用户对象设置为当前用户
    },
    clearUser(state) {
      state.user = null; // 清除用户状态
    },
  },
  actions: {
    login({ commit }, user) {
      // 在这里执行登录逻辑，比如发送登录请求到服务器
      // 如果登录成功，调用 mutation 来更新用户状态
      commit('setUser', user);
    },
    logout({ commit }) {
      // 在这里执行登出逻辑，比如清除本地存储的登录信息
      // 调用 mutation 来清除用户状态
      commit('clearUser');
    },
  },
  getters: {
    isLoggedIn: (state) => !!state.user, // 根据用户状态返回登录状态
  },
});
