<template>
  <div class="login-form">
    <h2>登录</h2>
    <div class="form-container">
      <form @submit.prevent="submitForm" class="left">
        <div class="form-group">
          <label for="username">账号:</label>
          <input type="text" id="username" v-model="username" required>
        </div>
        <div class="form-group">
          <label for="password">密码:</label>
          <input type="password" id="password" v-model="password" required>
        </div>
        <div class="form-group">
            <button type="submit" id='login' @click="login">登录</button>
        </div>
        <div class="form-group">
            <button type="submit" id='registration' @click="$router.push('/register')">注册</button>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
export default {
  data() {
    return {
      username: '',
      password: ''
    };
  },
  mounted() {
    this.checkLogin();
  },
  methods: {
    async checkLogin() {
		// eslint-disable-next-line
		const response = await axios.post('/api/session/');
		if (Object.keys(response.data).length === 0) {
			// TODO
		} else {
			if (response.data['type'] == 'student'){
				this.$router.push('/student');
			} else if (response.data['type'] == 'teacher') {
				this.$router.push('/teacher');
			}
			this.$store.dispatch('login', { username: response.data['username']});
		}
	},
    async submitForm() {
      try{
        // eslint-disable-next-line
        const response = await axios.post('/api/login/', {
          username: this.username,
          password: this.password
        });
		if (response.data['data']['type'] == 'student'){
			this.$router.push('/student');
		} else if (response.data['data']['type'] == 'teacher') {
			this.$router.push('/teacher');
		}
        this.$store.dispatch('login', { username: this.username});
      } catch (error) {
        // TODO
      }
    }
  }
};
</script>

<style scoped>

input, button {
  box-sizing: border-box;
}

.login-form {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 500px;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 30px;
  background-color: #ffffff;
}

.form-container {
  display: flex;
  padding-right:20px;
}

.form-container .left {
  flex: 1;
  margin-left:10px;
  margin-right:10px;
}

label {
  display: block;
}

input[type="text"],
input[type="password"],
input[type="file"] {
  width: 100%;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 10px;
}

input {
  margin-top: 10px;
  width: 100%;
}

.form-group {
    width: 100%;
    padding:10px;
}

#login{
    margin-top: 20px;
}

button {
  display: block;
  width: 100%;
  padding: 10px;
  margin-bottom: 10px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  font-weight: bold;
}
</style>
