<template>
  <div class="student-form">
    <h2>实时</h2>
    <div class="form-container">
       <div class="layout" id="center">
          <label for="status" id="statusLabel">您当前的状态未检测</label>
       </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import { mapGetters } from 'vuex';
export default {
  computed: {
    ...mapGetters(['isLoggedIn']),
  },
  mounted() {
    this.statusDetect();
  },
  methods: {
    async statusDetect() {
      await this.stopDetect();
      this.statusInterval = setInterval(async () => {
        try{
            if (this.isLoggedIn)
            {
                // eslint-disable-next-line
                const response = await axios.post('/api/status/')
                var statusLabel = document.getElementById("statusLabel");
                if (response.data['emotion']==''){
                    statusLabel.textContent = "您当前的状态未检测";
                } else {
                    statusLabel.textContent = "您当前的状态是："+response.data['emotion'];
                }
            } else {
                await this.stopDetect();
            }
        } catch (error) {
          await this.stopDetect();
        }
      }, 1000);
    },
    stopDetect() {
      // 清除定时器
      if (this.statusInterval) {
        window.clearInterval(this.statusInterval);
      } else {
        // TODO
      }
    }
  }
};
</script>

