<template>
  <div class="registration-form">
    <h2>注册</h2>
    <div class="form-container">
        <form @submit.prevent="submitForm" class="left">
            <div class="form-group">
            <label for="name">姓名:</label>
            <input type="text" id="name" v-model="name" required>
            </div>
            <div class="form-group">
            <label for="username">账号:</label>
            <input type="text" id="username" v-model="username" required>
            </div>
            <div class="form-group">
            <label for="password">密码:</label>
            <input type="password" id="password" v-model="password" required>
            </div>
            <div class="form-group">
            <label for="confirm-password">确认密码:</label>
            <input type="password" id="confirm-password" v-model="confirmPassword" required>
            </div>
            <div class="form-group">
            <label for="teachercode">教师验证码:</label>
            <input type="text" id="teachercode" v-model="teachercode" required>
            </div>
            <div class="form-group">
                <button type="submit">注册</button>
            </div>
        </form>
        <form @submit.prevent="submitForm" class="right">
            <div class="form-group">
            <label for="avatar">上传头像:</label>
            <input type="file" id="avatar" accept="image/*" @change="handleAvatarChange">
            <div v-if="avatarPreview" class="avatar-preview">
                <img :src="avatarPreview" alt="Avatar Preview">
            </div>
            <div v-else class="avatar-placeholder">
                <img src="../assets/avatar.png" alt="Avatar Placeholder">
            </div>
            </div>
        </form>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
export default {
  data() {
    return {
      name: '',
      username: '',
      password: '',
      confirmPassword: '',
      teachercode: '',
      avatar: null,
      avatarPreview: null
    };
  },
  methods: {
    async submitForm() {
        try{
            if (this.password==this.confirmPassword) { 
                const formData = new FormData();
                formData.append('name',this.name);
                formData.append('username', this.username);
                formData.append('password', this.password);
                formData.append('teachercode', this.teachercode);
                formData.append('avatar', this.avatar);
                // eslint-disable-next-line
                const response = await axios.post('/api/register/', formData, {
                headers: {
                    'Content-Type': 'multipart/form-data' // 设置请求头为multipart/form-data
                }
                });
                this.$router.push('/login')
            } else {
                // TODO
            }
        } catch (error) {
            // TODO
        }
    },
    handleAvatarChange(event) {
      // 处理上传头像的逻辑
      const file = event.target.files[0];
      if (file) {
        const reader = new FileReader();
		reader.onload = () => {
		// 将文件内容转换为Base64字符串
		const base64String = reader.result.split(',')[1];
		this.avatar = base64String;
		// 在这里可以将 base64String 发送到后端
		};
		reader.readAsDataURL(file);
        this.avatarPreview = URL.createObjectURL(file); // 用于预览的URL
      } else {
        this.avatar = null;
        this.avatarPreview = null;
      }
    }
  }
};
</script>

<style scoped>

input, button {
  box-sizing: border-box;
}

.registration-form {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 800px;
  border: 1px solid #ccc;
  border-radius: 30px;
  padding: 20px;
  background-color: #ffffff;
}

.form-container {
  display: flex;
  margin-bottom:10px;
}

.form-container .left {
  flex: 1;
  width:450px;
  margin-left:10px;
  margin-right:30px;
}

.form-container .right {
  flex: 1;
  margin-right: 30px;
}

label {
  display: block;
}

input[type="text"],
input[type="password"],
input[type="file"] {
  width: 100%;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 10px;
}

input {
  margin-top: 10px;
}

.form-group {
    width: 100%;
    padding:10px;
}

button {
  display: block;
  width: 100%;
  padding: 10px;
  margin-top: 20px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  font-weight: bold;
}

.avatar-preview {
  margin-top: 10px;
  width: 100%;
  text-align: center;
}

.avatar-placeholder {
  width: 100%;
  text-align: center;
  margin-top: 30px;
  border-radius: 30px;
}

.avatar-preview img {
  margin-top: 30px;
  max-width: 100%;
  max-height: 300px;
  border-radius: 30px;
}

.avatar-placeholder img {
  max-width: 100%;
  max-height: 300px;
  border-radius: 30px;
}
</style>
