<template>
  <div class="teacher-form">
    <h2>实时</h2>
    <div class="form-container">
       <div id="center">
          <div class="layout" id="left">
             <img id="video-img" alt="Video Placeholder">
          </div>
          <div class="layout" id="right">
             <svg id="pie-chart" alt="Visual Placeholder"></svg>
          </div>
       </div>
       <form @submit.prevent="submitForm">
          <div class="layout" id="bottom">
             <label for="addr">推流地址</label>
             <input type="text" id="videoaddr" v-model="videoaddr" required>
             <button type="submit" >连接</button>
         </div>
       </form>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import { mapGetters } from 'vuex';
export default {
  data() {
    return {
      addr: ''
    };
  },
  computed: {
    ...mapGetters(['isLoggedIn']),
  },
  methods: {
    async submitForm() {
      await this.stopDetect();
      const videoImageElement = document.getElementById('video-img');
      videoImageElement.src = ""
      console.log('reset')
      // eslint-disable-next-line
      const response = await axios.post('/api/detect/',{
          videoaddr: this.videoaddr
      });
      this.videoDetect();
      this.staticDetect();
    },
    async videoDetect() {
      this.videoInterval = setInterval(async () => {
        try{
			if (this.isLoggedIn)
			{
				// eslint-disable-next-line
				const response = await axios.post('/api/detect/')
				const videoImageElement = document.getElementById('video-img');
				videoImageElement.src = 'data:image/jpeg;base64,' + response.data['data']['videoframe'];
			} else {
				await this.stopDetect();
			}
        } catch (error) {
          await this.stopDetect();
        }
      }, 30);
    },
    async staticDetect() {
      this.staticInterval = setInterval(async () => {
        try{
			if (this.isLoggedIn)
			{
				// eslint-disable-next-line
				const response = await axios.post('/api/detect/')
				this.refreshPieChart(response.data['data']['emotions'])
			} else {
				await this.stopDetect();
			}
        } catch (error) {
          await this.stopDetect();
        }
      }, 1000);
    },
    stopDetect() {
      // 清除定时器
      if (this.videoInterval) {
        window.clearInterval(this.videoInterval);
      } else {
        // TODO
      }
      if (this.staticInterval) {
        window.clearInterval(this.staticInterval);
      } else {
        // TODO
      }
    },
    refreshPieChart(emotions){
        const counts = emotions.reduce((acc, label) => {
            acc[label] = (acc[label] || 0) + 1;
            return acc;
        }, {});
        
        const data = Object.entries(counts).map(([label, value]) => ({ label, value }));

        const width = 500;
        const height = 500;
        const radius = Math.min(width, height) / 2;
        // eslint-disable-next-line
        const svg = d3.select('#pie-chart')
            .attr('width', width)
            .attr('height', height)
            .append('g')
            .attr('transform', `translate(${width / 2},${height / 2})`);
        // eslint-disable-next-line
        const pie = d3.pie()
            .value(d => d.value);
        // eslint-disable-next-line
        const arc = d3.arc()
            .innerRadius(0)
            .outerRadius(radius);

        const arcs = svg.selectAll('arc')
            .data(pie(data))
            .enter()
            .append('g')
            .attr('class', 'arc');
        
        arcs.append('path')
            .attr('d', arc)
            // eslint-disable-next-line
            .attr('fill', (d, i) => d3.schemeCategory10[i]);
            
        arcs.append('text')
            .attr('text-anchor', 'middle')
            .attr('transform', d => {
                const [x, y] = arc.centroid(d);
                const angle = Math.atan2(y, x);
                const radius = Math.sqrt(x * x + y * y)/4;
                const offsetX = Math.cos(angle) * radius;
                const offsetY = Math.sin(angle) * radius;
                return `translate(${x + offsetX},${y + offsetY})`;
            })
            .style('font-size', '15pt')
            .text(d => d.data.label);

        arcs.append('text')
            .attr('text-anchor', 'middle')
            .attr('transform', d => {
                const [x, y] = arc.centroid(d);
                const angle = Math.atan2(y, x);
                const radius = Math.sqrt(x * x + y * y)/4;
                const offsetX = Math.cos(angle) * (radius); // 设置水平偏移量
                const offsetY = Math.sin(angle) * (radius); // 设置垂直偏移量
                return `translate(${x + offsetX},${y + offsetY})`;
            })
            .attr("dy", "25pt") // 添加垂直偏移量
            .style('font-size', '15pt')
            .text(d => d.data.value);
        }
    }
 }
</script>

