<template>
  <header class="fixed-title">学生课堂情感分析</header>
  <header id="user">
    <label id="status" v-if="isLoggedIn">你好，{{ $store.state.user.username }}</label>
    <button 
      @click="loginout" 
      :style="{ backgroundColor: isLoggedIn ? 'red' : '#007bff' }">
      {{ isLoggedIn ? '退出' : '登录' }}
    </button>
  </header>
  <div id="app">
    <router-view />
  </div>
</template>

<script>
import axios from 'axios';
import { mapGetters } from 'vuex';
export default {
  name: 'App',
  computed: {
    ...mapGetters(['isLoggedIn']),
  },
  methods: {
    async loginout() {
        if (this.isLoggedIn) {
          this.$store.dispatch('logout');
          // eslint-disable-next-line
          const response = await axios.post('/api/logout/');
          console.log('logout')
          this.$router.push('/login')
        } else {
          this.$router.push('/login')
        }
    },
  },
}
</script>

